@charset "utf-8";
a{text-decoration: none; color: #333;}

#login{
  margin-top: 90px;

}
#login > h2{
    font-weight: 800;
    font-size: 20px;
    line-height: 20px;
    text-align: center;
}
form{
  width: 340px;
  height: 296px;
  margin: 0 auto;
}
.user{
  margin-top: 10px;
  background-color: #fff;
  padding-left: 15px;
  width:345px;
  height: 54px;
  box-sizing:border-box;
}

.s_group{
  display: flex;
  justify-content: flex-end;
  font-size: 13px;
}
.s_group span{
  margin:0px 5px;
}

.login_btn, .join_btn{
  width:350px;height: 54px;
  border-radius: 5px;
  border: none;
  font-size: 16px;
  font-weight: 700;
  margin-top: 10px;
}
.login_btn{
  background-color: #5f0080;
  color: #fff;
}
.join_btn{
  border: solid 2px #5f0080;
  color: #5f0080;
  background: #fff;

}
.reset{
  margin: 10px 0px;
  border: solid 2px #333;
  color: #000;
  background: #fff;
  width:150px;height: 40px;
}
#login p{
  margin:0px 80px;
}
