@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.3.0/css/all.min.css);

*{margin:0px;padding:0px;}
ul,li,ol{
  list-style: none;
}
h3{
  text-align: center;
}
ul{
  margin:0 auto;
  width:98%;
  display: flex;
  gap: 10px;
}
ul li{
  position: relative;
  z-index: 10;
  width: 23%;
}
.img_wrap{
  width: 100%;
  height: 280px;
  position: relative;
  z-index: 10;
}
ul li img{
  width:100%;
  transition: .4s;
}
ul li img:hover{
  filter:brightness(1.1);
  transform :scale(1.1);
}

ul li p{
  line-height: 34px;
}
ul li p:first-child{
  position: relative;
  overflow: hidden;
  height: 260px;
  
}
ul li p:nth-child(2){
  font-weight: 600;
}
ul li p:nth-child(3){
  font-weight: 600;
  color: #f00;
}

ul li p:last-of-type{
  font-size: 14px;
  position: absolute;

}
